<template>
  <div>
    <ToolBar>
      <div>
        <el-input
          placeholder="用户名"
          size="small"
          style="width: 140px"
          v-model="searchParams.username"
          clearable
        ></el-input>

        <el-input
          placeholder="手机号"
          size="small"
          style="width: 140px"
          v-model="searchParams.phone"
          clearable
        ></el-input>

        <el-date-picker
          v-model="searchParams.dateRange"
          size="small"
          type="datetimerange"
          :picker-options="pickerOptions"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          align="right"
        ></el-date-picker>
        <el-button type="success" size="small" @click="refresh()">查询</el-button>
        <el-button type="warning" size="small" @click="clearSearchParams()">重置</el-button>
      </div>
    </ToolBar>
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      :row-style="{height:'8px'}"
      :cell-style="{padding:'0px 0px'}"
    >
      <el-table-column align="center" prop="username" label="用户名"></el-table-column>
      <el-table-column align="center" prop="phone" label="手机号"></el-table-column>
      <el-table-column align="center" prop="balance" label="可用金额"></el-table-column>
      <el-table-column align="center" prop="integration" label="积分"></el-table-column>
      <el-table-column align="center" prop="createTime" label="注册时间"></el-table-column>
      <el-table-column align="center" label="操作">
        <div slot-scope="s">
          <el-button type="primary" size="small" @click="edit(s.row,'recharge')">手动充值</el-button>
          <el-button type="danger" size="small" @click="edit(s.row,'koukuan')">手动扣款</el-button>
        </div>
      </el-table-column>
    </el-table>
    <Pagination
      :params="searchParams"
      :requestFunc="getUserList"
      ref="pagination"
      @returnData="returnData"
    />
    <UserEdit
      :title="titie"
      ref="edit"
      :showEditDialog="showEditDialog"
      @close="showEditDialog = false"
      @success="refresh"
    />
  </div>
</template>

<script>
import { getUserList } from "@/api/user/userManage";
import { getDateList } from "@/utils/date";
import { resetObject } from "@/utils/common";
import UserEdit from "./UserEdit";
export default {
  components: { UserEdit },
  data() {
    return {
      searchParams: {
        username: "",
        phone: "",
        dateRange: []
      },
      getUserList: getUserList,
      titie: "",
      total: 10,
      pickerOptions: getDateList(),
      showEditDialog: false,
      tableData: []
    };
  },
  methods: {
    clearSearchParams() {
      resetObject(this.searchParams);
      this.refresh();
    },
    refresh() {
      this.$refs.pagination.Refresh(); //分页刷新
      this.showEditDialog = false;
    },
    returnData(data) {
      this.tableData = data;
    },
    edit(row, type) {
      this.titie = type == "recharge" ? "手动充值" : "手动扣款";
      this.showEditDialog = true;
      this.$refs.edit.update(row.id, type);
    }
  }
};
</script>
